<template>
    <div class="home">
        <div class="bgc">
          <!-- 跟进提醒 -->
        <el-card class="box-card upRecordByRemind">
            <div v-if="isUpRecordByRemind">
                <div class="title">跟进提醒</div>
                <el-collapse v-model="activeNames"  v-for="(item,index) in upRecordByRemind" :key="index">
                    <el-collapse-item :title="item.customerName" :name="index" >
                          <div style="margin-bottom:15px;" >
                              <p class="upRecordByRemindTitle">跟进主题： </p>
                              <div v-html="item.nextFollowUpTheme" style="margin-left:80px;  margin-top:-20px;"></div>
                          </div>
                          <div >
                              <p  class="upRecordByRemindTitle">跟进内容:
                                <el-button  type="text" style="float:right" @click="targetHandle(item.followUpRecordId)" > 详情 </el-button>
                              </p>
                              <!-- { path: '/update/updateDetail', query: { id: followUpRecordId } } -->
                              <div v-html="item.nextFollowUpContent" style="margin-left:80px; margin-top:-20px;"></div>
                          </div>
                    </el-collapse-item>
                </el-collapse>
            </div>
            <div v-else class="noRemind"  style="font-size:14px; color: #78b9ee;">暂无更新提醒~</div>
        </el-card>
        </div>
    </div>
</template>
<script>
import Qs from 'qs'
export default {
  data () {
    return {
      drawer: false,
      activeNames: ['1'],
      upRecordByRemind: [],
      isUpRecordByRemind: false
    }
  },
  created () {
    this.getUpRecordByRemind()
    if (this.upRecordByRemind.length !== 0) {
      this.drawer = true
    }
  },
  methods: {
    targetHandle (id) {
      // console.log(id)
      this.$router.push({ path: '/update/updateDetail', query: { id: id } })
    },
    //   获取跟进提醒
    async getUpRecordByRemind () {
      const data = Qs.stringify({ userId: window.sessionStorage.getItem('userId') })
      const { data: res } = await this.$http.post('/followUpRecord/findFollowUpRecordByRemind', data)
      if (res.status !== 200) return this.$message.error('获取更新提醒失败！')
      this.upRecordByRemind = res.data
      // console.log(res.data)

      if (res.data.length >= 1) {
        this.isUpRecordByRemind = true
      }
      // console.log(res)
    },
    handleChange (val) {
      // console.log(val)
    }
  }
}
</script>
<style lang="less" scoped>

.upRecordByRemind{
    height: 93%;
    width: 300px;
    float: right;
    padding-bottom: 50px;
}
.title{
    text-align: center;
    margin-bottom: 10px;
    font-weight: 700;
    color: #76a6f1;
}
.upRecordByRemindTitle{
    font-weight: 700;
}
.noRemind{
  text-align: center;
  line-height: 520px;

}
.bgc{
  width:1340px;
  height: 768px;
  background-image: url(../assets/bgc.jpg);
  background-position: left;
  // background-image: url(img_flwr.gif), url(paper.gif);
    background-position: left top;
    background-repeat: no-repeat;
}

</style>
